import React, {useState, useEffect} from 'react';
import data from './data/book_store.json';

function Bookstore(props) {
    const [books, setBooks] = useState([]);

    useEffect(()=>{ // 
        fetch(`${process.env.PUBLIC_URL}/data/book_store.json`)
        .then(response=> response.json())
        .then(json => setBooks(json))
        .catch(err=>console.error('  :', err));
    }, []);

    return (
        <>
            <h4>Bookstore json </h4>
            <p>1.    import  ()</p>
            <ul>
                {data.map(book=>(
                    <li key={book.num}>
                        Num : {book.num}&nbsp;
                        NAME : {book.name}&nbsp;
                        1 : {book.area1}&nbsp;
                        2 : {book.area2}&nbsp;
                        3 : {book.area3}&nbsp;
                         : {book.BOOK_CNT}&nbsp;
                         : {book.owner_nm}&nbsp;
                         : {book.tel_num}&nbsp;
                    </li>
                ))}
            </ul>

            <p>2.    fetch    </p>
            <ul>
                {books.map(book=> (
                    <li key={book.num}>
                    Num : {book.num}&nbsp;
                    NAME : {book.name}&nbsp;
                    1 : {book.area1}&nbsp;
                    2 : {book.area2}&nbsp;
                    3 : {book.area3}&nbsp;
                     : {book.BOOK_CNT}&nbsp;
                     : {book.owner_nm}&nbsp;
                     : {book.tel_num}&nbsp;
                </li>
                ))}
            </ul>
        </>
    );
}

export default Bookstore;