import React, { Component } from 'react';
import './counter.css';

class Counter1 extends Component {
    //  ()
    state={n:1}

    onPlus = () => {
        // console.log('');
        this.setState({
            n:this.state.n+1
        });
    }
    
    onMinus = () => {
        // console.log('');
        if(this.state.n==1) {
            this.setState({
                n:this.state.n=1
            });
        } else {
            this.setState({
                n:this.state.n-1
            });
        }
    }

    render() {
        return (
            <>
                <p>+, -   ,  state </p>

                <div> : {this.state.n}</div>
                <input type="button" value="-" onClick={this.onMinus} className='btn minus'/>
                <input type="button" value="+" onClick={this.onPlus} className='btn plus'/>

                <p>     state </p>
                <p>setState() state      </p>
                <p>jsx      </p>
                <p>    </p>
            </>
        );
    }
}

export default Counter1;