import React, {useState} from 'react';

function Form1(props) {
    const [name, setName] = useState('');
    const nameChange = (e) => {
        setName(e.target.value);
    }
    const naemReset = (e) => {
        // alert('reset');
        setName('');
    }
    return (
        <>
            <h4>1.   </h4>
            <form>
                <p> .</p>
                <p>  : {name}</p>
                <input type='text' name='name' value={name} onChange={nameChange}/>&nbsp;
                <input type='button' value="" onClick={naemReset} />
            </form>

            <ul>
                <li>input   'onChange'  h4     </li>
                <li>HTML  DOM  </li>
                <li>React    </li>
                <li>     state </li>
            </ul>
        </>
    );
}

export default Form1;