import React, { useEffect, useState } from 'react';

function UseEffect2(props) {
    const [seconds, setSeconds] = useState(0);

    useEffect(()=> {
        const interval = setInterval(()=> {
            setSeconds(prev => prev + 1);
        }, 1000);
        return() => {
            clearInterval(interval);
            console.log(' ');
        }
    },[]);

    return (
        <>
            <h3>useEffect  -   </h3>
            <div> : {seconds}</div>
        </>
    );
}

export default UseEffect2;