import React, {use, useState} from 'react';

function Form4(props) {
    // const [name, setName] = useState('');
    // const [id_text, setId] = useState('');
    const [formData, setFormData] = useState({
        name: '',
        id_text : ''
    });

    // const nameChange = (e) => {
    //     setName(e.target.value);
    // }

    // const idChange = (e) => {
    //     setId(e.target.value);
    // }

    const nameChange = (e) => {
        const {name, value} = e.target;
        setFormData((prev)=>({ //es6  
            ...prev, 
            [name] : value
        }))
    }

    // const onDelete = (e) => {
    //     setName('');
    //     setId('');
    // }

    const onDelete = () => {
        setFormData({
            name: '',
            id_text : ''
        });
    }

    return (
        <>
            <h4>4.  input  </h4>
            <ul>
                <li>e.target.value :  </li>
                <li>e.target.name : name </li>
                <li>this.setState({`[]:`}) :      </li>
                <li>nameChange :     </li>
                <li>useState        </li>
            </ul>

            <form>
                <p>() : </p>
                <input type='text' id='name' name='name' value={formData.name} onChange={nameChange} />

                <p>() : </p>
                <input type='text' id='id' name='id_text' value={formData.id_text} onChange={nameChange} />
                
                <p>  {formData.name},  {formData.id_text}.</p>
                <input type='button' value='' onClick={onDelete} />
            </form>
        </>
    );
}

export default Form4;