import React, {useState} from 'react';

function Usestate(props) {
    const style = {
        width: '300px',
        padding: '10px',
        margin: '10px',
        border: '3px solid #f00'
    }

    const button = {
        padding: '5px 20px',
        border: 'none',
        margin: '0px 5px 0px 0px',
        borderRadius: '5px'
    }

    const [num, setNum] = useState(1);
    const price = 42750;
    const total = price*num;

    const onDecrease = () => {
        if(num===1) {
            setNum(1);
        } else {
            setNum(num-1);
        }
    }

    const onIncrease = () => {
        setNum(num+1);
    }

    return (
        <>
            <h3>useState</h3>
            <p>  state   .    state   ,   16.8 useState()    state    </p>
            <p>state   '  '   </p>
            <p>useState()  import React, useState   </p>

            <pre>
                const [,   ] = useState(); <br />
                const    = (); // <br />
                const      = useState(); <br />
                const    =    [0]; <br />
                const    =     [1]; <br />
            </pre>

            <div style={style}>
                <p>  : {num}</p>
                <input type='button' value='-' onClick={onDecrease} style={button}/>
                <input type='button' value='+' onClick={onIncrease} style={button}/>
                <p> : 7,300</p>
                <p>: {total}</p>
            </div>
        </>
    );
}

export default Usestate;