import React from 'react';

function StudentInput({name, age, email, onCreate, onDataChange}) {
    const input_style = {
        height: '40px',
        margin: '3px'
    }
    const flex = {display: 'flex'}
    const button = {padding: '5px 30px', lineHeight: '40px'}

    return (
        <div style={flex}>
            <input style={input_style} type='text' name='name' placeholder=' ' value={name} onChange={onDataChange}/>
            <input style={input_style} type='text' name='age' placeholder=' ' value={age} onChange={onDataChange}/>
            <input style={input_style} type='text' name='email' placeholder=' .' value={email} onChange={onDataChange}/>
            <button style={button} type='button' onClick={onCreate}> </button>
        </div>
    );
}

export default StudentInput;