import React from 'react';
import Main from './Main';

function Array(props) {
    //1 
    //const names = ['', '', '', ''];

    //2 
    const students = [
        {id:1, name:''},
        {id:2, name:''},
        {id:3, name:''},
        {id:4, name:''}
    ]

    const studentList = students.map((student)=>(<Main key={student.id} id={student.id} name={student.name}/>))
    return (
        <>
            <h3>     </h3>
            <p> ,     </p>
            <ul>
                <li>for</li>
                <li>while</li>
                <li>do whild</li>
            </ul>

            <ol>
                <li>for    : for(; ; )  ;</li>
                <li>map -             . map   '      '.          'map'.               </li>
                <li>   -      </li>
            </ol>
            <p>: array.map(callbackFunction(currentValue, index, array), thisArg)</p>
            <p>: array.filter(callback(currentValue, index, array), initialvalue)</p>

            <p></p>
            {studentList}

            <ul>
                <li>map  key  </li>
                <li>    , React           .(, ,  )</li>
                <li> key    DOM      key      </li>
                <li>map      props    </li>
                <li>key    .      index . (, index    index    )</li>
            </ul>
        </>
    );
}

export default Array;