import React, {useState, useRef} from 'react';
import './ref.css';

function UseRef(props) {
    const [inputs, setInputs] = useState({
        password: '',
        clicked:false,
        validated:false
    });

    const {password, clicked, validated} = inputs;
    const passwordInput= useRef();

    const onInputChange = (e) => {
        setInputs({
            password:e.target.value
        });
    }
    
    const onInputCheck = (e) => {
        e.preventDefault();
        setInputs({
            clicked:true,
            validated:password==='1234'
        });
        if(password!=='1234') {
            passwordInput.current.focus();
        }
    }

    const onInputReset = (e) => {
        e.preventDefault();
        setInputs ({
            password: ''
        });
    }
    return (
        <>
            <h3>useRef</h3>
            <p>
                ref '  DOM       '    hooks useRef     
            </p>
            <p>javascript: dom getElementById(), querySelector()</p>
            <p>jquery: dom $('')</p>
            <p> JSX DOM id   DOM     </p>
            <p>     id     .  Ref   ,        </p>

            <dl>
                <dt>DOM    </dt>
                <dd> input  </dd>
                <dd>  </dd>
                <dd>Canvas  </dd>
                <dd>....</dd>
            </dl>

            <h3>.      </h3>
            <p>      '',   ''  </p>
            <dl>
                <dt>ref, useRef    </dt>
                <dd>1.         ,         </dd>
                <dd>2.   DOM   ref, useRef      </dd>
            </dl>

            <form name='' method='' action='' className='form_style'>
                <fieldset>
                    <legend> </legend>
                    <p><input type='password' value={password} onChange={onInputChange} ref={passwordInput} className={clicked&&(validated? 'success':'failure')}/></p>
                    <p>
                        <input type='button' value='' onClick={onInputCheck}/>
                        <input type='button' value='' onClick={onInputReset}/>
                    </p>
                </fieldset>
                <br />
                <hr />
                <p>   : {password} </p>
            </form>
        </>
    );
}

export default UseRef;