import React, { useState, useRef } from 'react';
import Student from './Student';
import StudentInput from './StudentInput';

function StudentList(props) {
    const [students, setStudents] = useState([
        {
            id:'st001',
            name:'',
            age:28,
            email:'rm@gmail.com'
        },
        {
            id:'st002',
            name:'',
            age:30,
            email:'kim@gmail.com'
        },
        {
            id:'st003',
            name:'',
            age:25,
            email:'lee@gmail.com'
        },
        {
            id:'st004',
            name:'',
            age:35,
            email:'chan@gmail.com'
        },
        {
            id:'st005',
            name:'',
            age:29,
            email:'rm@gmail.com'
        }
    ]);

    const nextId = useRef(6);
    const [inputs, setInputs] = useState({
        name:'',
        age:'',
        email:''
    });

    //    state   
    const {name, age, email} = inputs;

    const onCreate = () => {
        if(!name.trim() || !email.trim() || !age) {
            alert(', ,    .');
            return;
        }
        const student = {
            id:'st00'+nextId.current,
            name,
            age:parseInt(age, 10),
            email
        }

        //  
        setStudents([...students,student]);

        setInputs({
            name:'',
            age:'',
            email:''
        });

        // nextId.current = nextId.current+1;
        nextId.current+=1;
    }

    const onDataChange = (e) => {
        const {name, value} = e.target;

        setInputs ({
            ...inputs,
            [name]:value
        });
    }

    const onRemove = (id) => {
        setStudents(students.filter((student)=> student.id!==id));
    }
    return (
        <>
            {/*    */}
            <StudentInput name={name} age={age} email={email} onCreate={onCreate} onDataChange={onDataChange}/>

            {/*    */}

            {/*   props    
            <ul>
                <li>
                    <Student student={students[0]} />
                </li>
                <li>
                    <Student student={students[1]} />
                </li>
                <li>
                    <Student student={students[2]} />
                </li>
                <li>
                    <Student student={students[3]} />
                </li>
                <li>
                    <Student student={students[4]} />
                </li>
            </ul> */}

            {/* map    */}
            <ul>
                {students.map((student, index)=>(
                    <li><Student student={student} key={index}onRemove={onRemove}/></li> 
                    //    index 
                ))}
            </ul>
        </>
    );
}

export default StudentList;