import React from 'react';

//function Child({name, age, hobby}){
const Child=({name, age, hobby})=>{
    return (
        <>
            <h3>Child( )</h3>
            <p>child </p>

            <ul>
                <li> : {name}</li>
                <li> : {age}</li>
                <li> : {hobby}</li>
            </ul>

            <p>children props        props</p>
            <p>   this.props.children   </p>
        </>
    );
}

Child.defaultProps = {
    name : '000',
    age : '00',
    hobby : '000'
}

export default Child;