import React, { useEffect, useState } from 'react';

function Form6(props) {
    const [description, setDescription] = useState(' textarea  ');

    const handleChange = (e) => {
        setDescription(e.target.value);
    }

    const [menu, setMenu] = useState('');

    const menuChange = (e) => {
        setMenu(e.target.value);
        console.log(e.target.value);
    }

    return (
        <>
            <h4>6. textarea, select </h4>
            <p>html textarea     React jsx textarea value=''  </p>

            <pre>
                *html <textarea></textarea>
                *jsx textarea value=
            </pre>

            <textarea name='content' id='content' cols='30' rows='10' value={description} onChange={handleChange}></textarea>
            <p>textarea   : {description}</p>

            <p>select</p>
            <pre>
                *
                <select>
                    <option value=''></option>
                    <option value=''>1</option>
                    <option value=''>2</option>
                    <option value=''>3</option>
                </select>

                *jsx
                <select value=''>
                    <option value=''></option>
                    <option value=''>1</option>
                    <option value=''>2</option>
                    <option value=''>3</option>
                </select>
                *select  value    
            </pre>

            <h4>   </h4>
            <select value={menu} onChange={menuChange}>
                <option value=''></option>
                <option value=''></option>
                <option value=''></option>
                <option value=''></option>
                <option value=''></option>
                <option value=''></option>
                <option value=''></option>
                <option value=''></option>
                <option value=''></option>
            </select>
            <p>    {menu}</p>

            <ul>
                <li>coloe.log(menu) - useState       </li>
                <li>   : setMenu(e.target.value); console.log(menu);</li>
                <li> 1 : console.log(e.target.value);</li>
                <li> 2 : useEffect </li>
                <pre>
                    {/* import { useEffect } from 'react' 
                         
                        useEffect(() => {
                            console.log(' ', menu); <br />
                        }, [menu]); */}
                </pre>
            </ul>
        </>
    );
}

export default Form6;