import React from 'react';
import product from '../images/product1.png';

function Class4(props) {
    return (
        <>
            <h3 className='title3'>02   </h3>
            <ul>
                <li>public     3 
                    <ol>
                        <li>src={`${process.env.PUBLIC_URL}//.`}</li>
                        <li>src={'.///'}</li>
                        <li>src={'//'}</li>
                    </ol>
                </li>
                <li>1. src    : import
                    <ol>
                        <li>import logo from "./src//."</li>
                        <li>img src={} alt="" </li>
                    </ol>
                </li>
                
                <li>2. src    : require
                    <p>img src={require('../images/logo.png').default} alt=""</p>
                </li>
                <li>3. css   
                    <ol>
                        <li>background url: (" /.")</li>
                        <li>background : url("/.")</li>
                        <li>background : url(".//.")</li>
                    </ol>
                </li>
            </ul>

            <img src={`${process.env.PUBLIC_URL}/images/logo.png`} alt=" "/>
            <br />
            <img src={product} alt=""/>
            <br />
            <img src={require('../images/product2.png')} alt=""/>
            
            <p>jsx  public   </p>
            <p>css  src  </p>
            <p> jsx   PUBLIC_URL   (github  PUBLIC_URL    )</p>
        </> 
    );
}

export default Class4;