import React, { useState } from 'react';

function Form5(props) {
    const [formData, setFormData] = useState({
        name: '',
        age: null
    });

    const nameChange = (e) => {
        const {name, value} = e.target;
        setFormData((prev)=>({
            ...prev,
            [name] : value
        }));
    }

    const dataSubmit = (e) => {
        e.preventDefault();
        const age = formData.age;
        if(!Number(age)) {
            alert('   ');
        } else {
            console.log(' :' , formData);
        }
    }

    return (
        <>
            <h4>5.    </h4>
            <p>,    ,              </p>
            <p>if  state     'alert'  </p>
            <p>   Number  </p>

            <form onSubmit={dataSubmit}>
                <p><strong> : {formData.name}</strong></p>
                <p><strong> : {formData.age}</strong></p>

                <p>  :&nbsp;
                    <input type='text' name='name' value={formData.name} onChange={nameChange}/>
                </p>

                <p>  : &nbsp;
                    <input type='text' name='age' value={formData.age || ''} onChange={nameChange}/>
                </p>

                <button type='submit'></button>
            </form>
        </>
    );
}

export default Form5;