import React, {useState} from 'react';

const Counter2 = (props) => {
    const [num, setNum] = useState(1);

    const onPlus1 = () => {
        setNum(num+1);
    }
    
    const onMinus1 = () => {
        // console.log('');
        if(num===1) {
            setNum(1);
        } else {
            setNum(num-1);
        }
        // ==:   
        // ===: ,   
    }
    return (
        <>
            <div> : {num}</div>
            <input type="button" value="-" onClick={onMinus1} className='btn minus'/>
            <input type="button" value="+" onClick={onPlus1} className='btn plus'/>
        </>
    );
}

export default Counter2;