import React, {useState} from 'react';
import Usestate from './Usestate';

function LoginForm(props) {
    const form = {
        padding: '0px 20px',
        width: '500px'
    }

    const input_s = {padding: '5px'}

    const [input, setInput] = useState({
        username: '',
        userid: ''
    });

    const {username, userid} = input;

    const onDataChange = (e) => {
        const {value, name}= e.target;
        setInput({
            ...input, 
            [name]:value
        });
    }

    const onDataReset = (e) => {
        setInput({
            username: '',
            userid: ''
        });
    }

    return (
        <>
            <h3> </h3>
            <ul>
                <li> input    name, value</li>
                <li>e.target.value</li>
                <li>e.target.name</li>
            </ul>

            <fieldset style={form}>
                <legend> </legend>
                <p>
                    <input type='text' value={userid} name='userid' placeholder=' ' style={input_s} onChange={onDataChange}/>
                </p>
                <p>
                    <input type='text' value={username} name='username' placeholder=' ' style={input_s} onChange={onDataChange}/>
                </p>
                <input type='button' value=' ' style={input} onClick={onDataReset}/>

                <p>  :{username}:({userid})</p>
            </fieldset>

            <h3></h3>
            <pre>
                const [input, setInputs] = useState;
                // 1  useState()     <br />
                //     2  2    <br />
                //  useState()   
            </pre>
            <p>      <br />
                //input.username; <br />
                //input.userid; <br />
            </p>
            <p>
                //    <br />
                const username, userid = input;
            </p>
            <p>
                //    <br />
                username; <br />
                userid; <br />
                //       
            </p>
            <p>
                const  value, name  = e.target; <br />
                //  e.target name, value     =  
                <br /><br />
                setInput(<br />
                ...input, <br />
                [name]:value<br />
                )
                <br /><br />
                // input      ...  <br />        
            </p>
            <p>
                //[name]:value     name:value     <br />  key[]        key 
                <br /><br />
                //          
            </p>
        </>
    );
}

export default LoginForm;